body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    width: 100%;
    margin: 0;
    padding: 0% 0% 0% 2%;
    /* background: darkgrey; */

    -webkit-user-select: none;  /* Chrome all / Safari all */     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */
}

.header-title{
    color: darkgreen;
}
.header-url{
    text-decoration: none;
}
.header-url:active, .header-url:visited, .header-url:hover, .header-url:link{
    text-decoration: none;
    color: darkgreen;
}

.navbar{
    width: 100%;
    -webkit-user-select: none;  /* Chrome all / Safari all */     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */
}

.navbar-block{
    background-color: yellowgreen;
    list-style-type: none;
    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.navbar-block #active{
    background-color: darkgreen;
    color: white;
}

.navbar-option{
    /* background-color: yellowgreen; */
    width: auto;
    padding: 0% 2%;
    text-align: center;
    cursor: pointer;
}

.navbar-option:hover{
    background-color: darkgreen;
    color: white;
}
.navbar-option .navbar-title{
    font-size: 1.5rem;
}


.footer{
    width: 100%;
    border-top: 0.25px solid silver;
    -webkit-user-select: none;  /* Chrome all / Safari all */     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

